* {
  box-sizing: border-box;
  margin-left: 0;
  margin-right: 0;
  /* margin-top: 0; */
  outline: 0;
}
.natureContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}
/* Position the image container (needed to position the left and right arrows) */
.containernature {
  position: relative;
  width: 95%;
}

/* Hide the images by default */
.mySlides {
  display: none;
  text-align: center;
  background-color: rgb(242, 244, 241);
  height: 500px;
}
.mySlides:before {
  content: "";
  height: 100%;
  display: inline-block;
  vertical-align: middle;
}
.mySlides img {
  /* left: 50%;
  transform: translate(-50%,0);
  position: absolute; */
  /* margin-right: auto;
  margin-left: auto;
  margin-top: auto;
  margin-bottom: auto; */
  /* position: absolute; */
  /* margin: 0; */
  /* top: 0; */
  /* left: 0;
  right: 0; */
  /* bottom: 0; */

  display: inline-block;
  vertical-align: middle;

  max-width: 100%;
  max-height: 100%;
  /* all side shadow */
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  /* three side shadow */
  /* box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12); */
}
.contactnote {
  position: absolute;
  margin-right: auto;
  margin-left: auto;
  right: 0;
  left: 0;
  top: 25%;
  width: 90%;
  font-size: 1rem;
}
p > a {
  color: blue;
}
/* Add a pointer when hovering over the thumbnail images */
.cursor {
  cursor: pointer;
}

/* Next & previous buttons */
.prev,
.next {
  cursor: pointer;
  position: absolute;
  top: 15%;
  width: auto;
  color: blue;
  font-weight: bold;
  font-size: 25px;
  user-select: none;
  -webkit-user-select: none;
}

/* Position the "next button" to the right */
.next {
  right: 0;
}
.prev {
  left: 0;
}

button {
  border: none;
  height: 4rem;
  background-color: Transparent;
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover,
.next:hover {
  background-color: black;
  color: white;
}

/* Container for image text */
.imagesize {
  font-size: 15px;
}
#caption {
  width: 45%;
  text-align: center;
  color: black;
  font-size: 1.5rem;

  /* margin: 1rem; */
  margin-right: auto;
  margin-left: auto;
  /* text-shadow: -4px 3px 0 #000, -14px 7px 0 #0a0e27; */
}

.row {
  max-height: 150px;
}
.row {
  position: relative;
  display: flex;
  justify-content: center;
  top: 2rem;
}
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Six columns side by side */
.column {
  margin-right: 5px;
}
.column:last-child {
  margin-right: 0px;
}
.column img {
  max-width: 100%;
  max-height: 9rem;
  text-align: center;
  vertical-align: middle;
}
/* .notesymbol {
  position: absolute;
  top: 50%;
  right: 10%;
