} */

/* Add a transparency effect for thumnbail images */
.demo {
  opacity: 1;
}

.active,
.demo:hover {
  opacity: 0.6;
}

.footerdescription {
  margin-top: 5rem;
}
.copyright {
  width: 95%;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 5rem;
}
